import 'whatwg-fetch';
import Promise from 'promise-polyfill';
import React from 'react';
import { render } from 'react-dom';

import '@transferwise/comparison-components/dist/comparison-components.css';

import { Comparison, ComparisonTable } from '@transferwise/comparison-components';
import displayPlaceholder from './placeholder';

import './style.css';

const STYLE_HOST = 'components/comparison-components/dist/widgets.css';
// const STYLE_HOST = 'https://widgets.transferwise.com/widgets.css';

const { head } = document;
const link = document.createElement('link');
link.type = 'text/css';
link.rel = 'stylesheet';
link.href = STYLE_HOST;
head.appendChild(link);

function extractRequiredParamsFromUrl (urlString) {
  const [, query] = urlString.split('?');
  const params = {};
  const queryString = query.split('&');

  for (let i = 0; i < queryString.length; i++) {
    const pair = queryString[i].split('=');
    params[pair[0]] = pair[1]; // eslint-disable-line prefer-destructuring
  }
  return params;
}

function parseValues (dataset) {
  const transformed = { ...dataset };
  transformed.excludeCheaper = dataset.excludeCheaper && dataset.excludeCheaper !== 'false';
  transformed.expandDisclaimer = dataset.expandDisclaimer && dataset.expandDisclaimer !== 'false';
  transformed.maxVisibleProviders = dataset.maxVisibleProviders && +dataset.maxVisibleProviders;
  transformed.providers = dataset.providers && dataset.providers.split(',');
  return transformed;
}

function extractDataAttrs (elem) {
  return { ...extractRequiredParamsFromUrl(elem.href), ...parseValues(elem.dataset) };
}

function createContainer (elem) {
  const container = document.createElement('div');
  elem.parentNode.replaceChild(container, elem);
  return container;
}

const forEach = (array, callback, scope) => {
  for (let i = 0; i < array.length; i++) {
    callback.call(scope, i, array[i]);
  }
};

function removePlaceholder () {
  const clientElements = document.querySelectorAll('.loader-container');
  forEach(clientElements, (index, elem) => {
    elem.parentElement.removeChild(elem);
  });
}

function run (clientElements) {
  removePlaceholder();

  forEach(clientElements, (index, elem) => {
    const attrs = extractDataAttrs(elem);
    const container = createContainer(elem);
    container.className = 'Comparison';

    const widgets = {
      'tw-comparison': () => <Comparison {...attrs} />,
      'tw-comparison-table': () => <ComparisonTable {...attrs} />,
    };

    render(widgets[elem.className](), container);
  });
}

const clientElements = document.querySelectorAll('.tw-comparison, .tw-comparison-table');

forEach(clientElements, (index, elem) => {
  displayPlaceholder(elem);
});

link.onload = () => run(clientElements);

if (!window.Promise) {
  window.Promise = Promise;
}



