function createContainer (elem, className) {
  const container = document.createElement(elem);
  container.className = className;
  return container;
}

function createContainerNS (namespaceURI, elem) {
  return document.createElementNS(namespaceURI, elem);
}

function createOuterFlag () {
  const outerFlag = createContainerNS('http://www.w3.org/2000/svg', 'svg');
  outerFlag.setAttributeNS('http://www.w3.org/2000/xmlns/', 'xmlns:xlink', 'http://www.w3.org/2000/svg');
  outerFlag.setAttributeNS(null, 'viewBox', '-2 -2 56 56');
  outerFlag.setAttributeNS(null, 'width', '52');
  outerFlag.setAttributeNS(null, 'height', '52');
  return outerFlag;
}

function createFlagStroke () {
  const flagStroke = createContainerNS('http://www.w3.org/2000/svg', 'polygon');
  flagStroke.setAttribute('class', 'loader-flag-stroke');
  flagStroke.setAttributeNS(null, 'stroke', '#00B9FF');
  flagStroke.setAttributeNS(null, 'stroke-width', '2');
  flagStroke.setAttributeNS(null, 'stroke-linejoin', 'mitter');
  flagStroke.setAttributeNS(null, 'stroke-linecap', 'round');
  flagStroke.setAttributeNS(null, 'stroke-miterlimit', '10');
  flagStroke.setAttributeNS(null, 'stroke-dasharray', '300');
  flagStroke.setAttributeNS(null, 'stroke-dashoffset', '300');
  flagStroke.setAttributeNS(null, 'fill', 'none');
  flagStroke.setAttributeNS(null, 'points', '26.3,52 19.5,52 39.2,5.5 16.8,5.5 21.6,13.6 13.4,21.8 27,21.8 24.6,27.3 0,27.3 14.3,13.7 6.1,0 48.2,0');
  return flagStroke;
}

function createInnerFlag () {
  const innerFlag = createContainerNS('http://www.w3.org/2000/svg', 'svg');
  innerFlag.setAttributeNS('http://www.w3.org/2000/xmlns/', 'xmlns:xlink', 'http://www.w3.org/2000/svg');
  innerFlag.setAttributeNS(null, 'viewBox', '0 2 52 48');
  innerFlag.setAttributeNS(null, 'width', '52');
  innerFlag.setAttributeNS(null, 'height', '52');
  innerFlag.setAttribute('class', 'loader-flag-fill');
  return innerFlag;
}

export default function placeholder (elem) {
  const loaderContainer = createContainer('div', 'loader-container');
  loaderContainer.style.cssText = 'text-align:center;';

  const loader = createContainer('div', 'loader');
  const loaderSpinner = createContainer('div', 'loader-spinner');
  loaderSpinner.style.cssText = 'background-image: url(data:image/svg+xml;base64,);';
  const loaderFlag = createContainer('div', 'loader-flag');

  const flagFill = createContainer('div', 'loader-flag-fill');

  const outerFlag = createOuterFlag();
  const flagStroke = createFlagStroke();
  outerFlag.appendChild(flagStroke);

  const innerFlag = createInnerFlag();
  innerFlag.appendChild(flagFill);

  loaderFlag.appendChild(outerFlag);
  loaderFlag.appendChild(innerFlag);

  loader.appendChild(loaderSpinner);
  loader.appendChild(loaderFlag);

  loaderContainer.appendChild(loader);
  elem.parentNode.insertBefore(loaderContainer, elem);
}



